  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  width: 10px;
  height: 10px;
  cursor: pointer;
  display: inline-block;
  margin: 0 8px;
}
.carousel .control-dots .dot.selected,
.carousel .control-dots .dot:hover {
  opacity: 1;
  background: rgba(0, 0, 0, 0.9);
}
.carousel .carousel-status {
